// tests/integration/like-restaurant.test.js
import FavoriteRestaurantIdb from '@data/favorite-restaurant-idb';
import * as TestFactories from './helpers/test-factories';
import 'fake-indexeddb/lib/FakeIndexedDB';
import { describe, it, expect } from '@jest/globals';


describe('Liking A Restaurant - Comprehensive Test Suite', () => {
  // Setup dan cleanup untuk setiap test
  const addLikeButtonContainer = () => {
    document.body.innerHTML = '<div id="likeButtonContainer"></div>';
  };

  beforeEach(() => {
    addLikeButtonContainer();
  });

  afterEach(async () => {
    await FavoriteRestaurantIdb.deleteAll();
  });

  // Kasus Positif - Fitur Dasar
  describe('Basic Liking Functionality', () => {
    it('should be able to like a restaurant with complete information', async () => {
      const restaurant = {
        id: 1,
        name: 'Test Restaurant',
        city: 'Test City',
        rating: 4.5,
      };

      await TestFactories.createLikeButtonPresenterWithRestaurant(restaurant);
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));

      const savedRestaurant = await FavoriteRestaurantIdb.getRestaurant(1);
      expect(savedRestaurant).toEqual(restaurant);
    });

    it('should increase favorite restaurants count after liking', async () => {
      await TestFactories.createLikeButtonPresenterWithRestaurant({ id: 1 });
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));

      const favoriteRestaurants = await FavoriteRestaurantIdb.getAllRestaurants();
      expect(favoriteRestaurants.length).toBe(1);
    });
  });

  // Kasus Negatif - Validasi Input
  describe('Negative Scenarios for Liking', () => {
    it('should not add restaurant without id', async () => {
      await TestFactories.createLikeButtonPresenterWithRestaurant({});
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));

      const favoriteRestaurants = await FavoriteRestaurantIdb.getAllRestaurants();
      expect(favoriteRestaurants.length).toBe(0);
    });

    it('should not allow duplicate likes', async () => {
      const restaurant = { id: 1, name: 'Test Restaurant' };
      
      await TestFactories.createLikeButtonPresenterWithRestaurant(restaurant);
      
      // Like pertama
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));
      
      // Reset container
      addLikeButtonContainer();
      await TestFactories.createLikeButtonPresenterWithRestaurant(restaurant);
      
      // Like kedua
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));

      const favoriteRestaurants = await FavoriteRestaurantIdb.getAllRestaurants();
      expect(favoriteRestaurants.length).toBe(1);
    });
  });

  // Kasus Edge Case
  describe('Edge Case Scenarios', () => {
    it('should handle liking a restaurant with minimal information', async () => {
      const restaurant = { id: 1 };
      await TestFactories.createLikeButtonPresenterWithRestaurant(restaurant);
      
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));
      const savedRestaurant = await FavoriteRestaurantIdb.getRestaurant(1);
      
      expect(savedRestaurant).toEqual(restaurant);
    });

    it('should not break when trying to like an already deleted restaurant', async () => {
      const restaurant = { id: 1 };
      
      // Simulasi restoran sudah dihapus
      await FavoriteRestaurantIdb.deleteRestaurant(1);
      
      await TestFactories.createLikeButtonPresenterWithRestaurant(restaurant);
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));

      const favoriteRestaurants = await FavoriteRestaurantIdb.getAllRestaurants();
      expect(favoriteRestaurants.length).toBe(1);
    });
  });

  // Validasi UI dan Interaksi
  describe('UI Interaction Validation', () => {
    it('should change like button state after clicking', async () => {
      await TestFactories.createLikeButtonPresenterWithRestaurant({ id: 1 });
      
      const likeButton = document.querySelector('#likeButton');
      likeButton.dispatchEvent(new Event('click'));

      expect(likeButton.getAttribute('aria-label')).toBe('unlike this restaurant');
    });

    it('should show correct UI state for liked restaurant', async () => {
      const restaurant = { id: 1, name: 'Test Restaurant' };
      await TestFactories.createLikeButtonPresenterWithRestaurant(restaurant);
      
      // Simulate like
      document.querySelector('#likeButton').dispatchEvent(new Event('click'));

      // Check UI elements or classes that indicate liked state
      const likedIndicator = document.querySelector('.liked-restaurant-indicator');
      expect(likedIndicator).toBeTruthy();
    });
  });

  // Performance dan Keamanan
  describe('Performance and Security Checks', () => {
    it('should handle multiple rapid like/unlike actions', async () => {
      const restaurant = { id: 1 };
      
      await TestFactories.createLikeButtonPresenterWithRestaurant(restaurant);
      
      // Simulasi klik cepat
      for (let i = 0; i < 5; i++) {
        document.querySelector('#likeButton').dispatchEvent(new Event('click'));
      }

      const favoriteRestaurants = await FavoriteRestaurantIdb.getAllRestaurants();
      expect(favoriteRestaurants.length).toBe(1);
    });
  });
});